.container-contact {
    max-width: 420px;
    margin: 50px auto;
    background-color: #121a2a;
    padding: 40px 30px;
    border-radius: 12px;
    box-shadow: 0 0 20px #000a24;
    font-family: 'Lato', sans-serif;
    color: #eee;
  }
  
  .form-card {
    display: flex;
    flex-direction: column;
    gap: 18px;
  }
  
  .form-header {
    font-size: 2.4rem;
    margin-bottom: 20px;
    color: #f5c518;
    text-align: center;
  }
  
  .input {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .input-field {
    padding: 12px 15px;
    border-radius: 8px;
    border: 1.5px solid #555;
    background-color: #1f2a45;
    color: #eee;
    font-size: 1rem;
    outline-offset: 2px;
    transition: border-color 0.3s;
  }
  
  .input-field:focus {
    border-color: #f5c518;
    background-color: #22325a;
  }
  
  .submit-button {
    padding: 14px 0;
    border: none;
    border-radius: 8px;
    background-color: #f5c518;
    color: #121a2a;
    font-weight: 700;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .submit-button:hover {
    background-color: #d4ae14;
  }
  
  .message {
    margin-top: 15px;
    font-size: 1.1rem;
    text-align: center;
  }
  