.home-section {
  max-width: 900px;
  margin: 60px auto;
  padding: 0 20px;
  color: #f5c518;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
}

.home-title {
  font-size: 3rem;
  font-weight: 900;
  margin-bottom: 40px;
}

.slider {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #121a2a;
  border-radius: 12px;
  box-shadow: 0 0 15px #d4af37cc;
  padding: 20px;
}

.slide {
  max-width: 700px;
  text-align: center;
  color: #d4af37cc;
}

.slide-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 0 0 10px #f5c518cc;
  margin-bottom: 15px;
}

.slide-text h2 {
  font-size: 1.8rem;
  margin-bottom: 8px;
  color: #f5c518;
}

.slide-text p {
  font-size: 1.1rem;
  line-height: 1.4;
}

.slider-btn {
  background-color: transparent;
  border: none;
  color: #f5c518;
  font-size: 2.5rem;
  cursor: pointer;
  padding: 10px 15px;
  user-select: none;
  transition: color 0.3s ease;
}

.slider-btn:hover {
  color: #ffd700;
}

.left-btn {
  margin-right: 20px;
}

.right-btn {
  margin-left: 20px;
}
