.about-section {
  max-width: 900px;
  margin: 50px auto;
  padding: 30px;
  background: linear-gradient(135deg, #121a2a 0%, #1a233a 100%);
  border-radius: 15px;
  box-shadow: 0 0 25px #d4af37cc;
  color: #f5c518;
  font-family: 'Montserrat', sans-serif;
}

.about-background-circle {
  position: absolute;
  width: 250px;
  height: 250px;
  background: #d4af37a0;
  border-radius: 50%;
  top: -100px;
  right: -100px;
  z-index: 0;
}

.about-container {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  position: relative;
  z-index: 1;
}

.about-image-wrapper {
  flex: 1 1 300px;
  max-width: 400px;
}

.about-image {
  width: 100%;
  border-radius: 12px;
  box-shadow: 0 0 15px #ffd700bb;
  object-fit: cover;
}

.about-text-block {
  flex: 2 1 400px;
}

.about-title {
  font-size: 2.8rem;
  margin-bottom: 20px;
  font-weight: 900;
  color: #ffd700;
  text-shadow: 0 0 10px #d4af37cc;
}

.about-paragraph {
  font-size: 1.1rem;
  line-height: 1.5;
  margin-bottom: 15px;
  color: #f0e68c;
}

.bold {
  font-weight: 700;
  color: #fffacd;
}

.about-list {
  list-style: inside disc;
  margin-bottom: 20px;
  color: #fff8b8;
  font-size: 1.1rem;
  line-height: 1.5;
}
