.service-container {
  max-width: 900px;
  margin: 50px auto;
  padding: 20px;
  background-color: #121a2a;
  border-radius: 12px;
  box-shadow: 0 0 20px #d4af37aa;
  color: #f5c518;
  font-family: 'Montserrat', sans-serif;
}

.service-title {
  font-size: 2.8rem;
  font-weight: 900;
  margin-bottom: 20px;
  text-align: center;
  color: #ffd700;
  text-shadow: 0 0 10px #d4af37cc;
}

.service-intro {
  font-size: 1.2rem;
  margin-bottom: 25px;
  line-height: 1.5;
  color: #f0e68c;
}

.service-subtitle {
  font-size: 1.8rem;
  font-weight: 700;
  margin-top: 25px;
  margin-bottom: 15px;
  color: #f5c518;
  border-bottom: 2px solid #d4af37;
  padding-bottom: 6px;
}

.service-list {
  list-style: none;
  padding-left: 0;
  color: #f0e68c;
  font-size: 1.1rem;
  line-height: 1.5;
}

.service-list li {
  padding: 8px 0;
  border-bottom: 1px solid #333f5a;
}

.service-contact {
  font-size: 1.1rem;
  margin-top: 25px;
  color: #fffacd;
  font-weight: 600;
}

.service-note {
  margin-top: 20px;
  font-style: italic;
  color: #e6d060;
  font-size: 1rem;
}
