.car-page {
    max-width: 600px;
    margin: 30px auto;
    padding: 20px;
    background: #111;
    color: #eee;
    border-radius: 8px;
    font-family: 'Montserrat', sans-serif;
  }
  
  .car-image-large {
    width: 100%;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  .car-page ul {
    list-style: none;
    padding-left: 0;
  }
  
  .car-page li {
    margin-bottom: 8px;
    font-size: 1.1rem;
  }
  